<template>
   <div id="index_header">
       <header class="index-header clearfix">
           <i class="user-icon" @click="goUserInfoPage()"></i>
           <ul class="index-infos">
               <router-link :to="'/news/'+navBar.id" class="head-item current" id="newsTag">
                   新闻
               </router-link>
               <router-link to="/tiezi" class="head-item" >
                   文章
               </router-link>
               <router-link to="/database" class="head-item">
                   资料库
               </router-link>
           </ul>
           <router-link class="search-icon" to="/search"></router-link>
       </header>
   </div>
</template>
<script>
    import {getNewsTab} from '../../api/news'
    import Api from '../../api/api';
    export default{
        data(){
          return{
              navBar:{id:0,name:"xxx"},
          }
        },
        mounted(){
              this.getNavData();
              this.initXEvent();
        },
        methods:{
          initXEvent(){
            const _this= this;
            $("#newsTag").on('click',function () {
              sessionStorage.removeItem("width");
              sessionStorage.removeItem("index");
              _this.$router.go(0);
            })
          },
          getNavData(){
            const that = this;
            getNewsTab(Api.CATEGORYS).then((res) => {
              if (res.status === 200 && res.data !== null) {
                let arrNav = res.data.data.detail;
                for(let i = 0 ;i<arrNav.length;i++){
                    if(i ===0){
                        that.navBar.id = arrNav[i].id;
                        that.navBar.name = arrNav[i].name;
                    }
                }
              }
            }, (err) => {

            });
          },
            /**
             * go to search page
             */
            goSearchPage(){
                this.$router.push({path: '/search', replace: true});
            },
            /**
             * go to userInfo page
             */
            goUserInfoPage(){
                this.$router.push({path: '/personalcenter', replace: true});
            }
        }
    }
</script>




